<template>
  <div class="modal">
    <div class="modal-content">
      <a-form-model
        layout="vertical"
        ref="copyForms"
        :rules="rules"
        :model="copyForm"
      >
        <a-row :gutter="24">
          <a-col :span="12">
            <a-form-model-item class="labelStyle" name="value" label="数据">
              <a-input
                name="value"
                type="text"
                v-model:value="copyForm.value"
                disabled
                style="width: 200px"
              />
            </a-form-model-item>
          </a-col>
          <a-col :span="12">
            <a-form-model-item
              class="labelStyle"
              label="复制数据"
              prop="copyValue"
            >
              <a-input
                name="copyValue"
                type="text"
                v-model:value="copyForm.copyValue"
                style="width: 200px"
                :max-length="200"
              />
            </a-form-model-item>
          </a-col>
        </a-row>
      </a-form-model>
    </div>
    <div class="modal-footer">
      <g-btn text="确认" @callback="save" type="primary"></g-btn>

      <g-btn text="取消" @callback="$emit('closeModal')"></g-btn>
    </div>
  </div>
</template>

<script>
import loginVue from "@/layout/login/login.vue";

export default {
  name: "GCopyNdo",
  props: {
    entity: {
      type: Object,
      default: () => {
        return {};
      },
    },
  },
  data() {
    return {
      copyForm: {
        value: "",
        copyValue: "",
      },
      rules: {
        copyValue: [
          { required: true, message: "请输入目标实例名称", trigger: "change" },
        ],
        
      },
    };
  },

  mounted() {
    this.copyForm = this.entity;
    // 拼接 Copy-of-
    this.copyForm.copyValue = `Copy-of-${this.copyForm.value}`;
  },

  methods: {
    save() {
      // 校验
      this.$refs.copyForms.validate().then((valid) => {
        if (!valid) {
          return;
        }
        this.$emit("success", this.copyForm);

        this.$emit("closeModal");
      });
    },
  },
};
</script>

<style lang="scss" scoped></style>
